import { getApi,postApi, putApi } from "../../api/api";
import {dateFormatter} from '../../dateFormatter'

const state = {
   customersAll:[
       ],
       customers:[],
       customersBranch:[]
}

const getters= {
  getCustomers(state){
    return state.customersAll;
  }
}
const mutations= {
  
  setNewCustomer(state,payload){
      // console.log(payload);
      let newId = (state.customersAll[0].id+1)
      payload.setupDate = dateFormatter(payload.setupDate)
    state.customersAll.unshift({...payload,id:newId})
    // console.log(state.customersAll);
    
  //  state.customersAll.unshift(payload)
  },
  setCustomersForBranch(state,payload){
    state.customers=[]  
    // console.log(payload,'work');
   state.customersAll.filter(response=>{
    if(payload.companyId == 0)
    {
      state.customers = state.customersAll
    }
    else  if(response.branch == payload.companyId && response.isActive)
    {
      state.customers.push(response)
    }
   })

    
  },
  setOrderTable(state,payload){
    state.customersAll = payload.body
  }
}

const actions= {
  initOrdersCompanyTable(VuexContext,payload){
    return getApi('orders?filter=companyId:'+payload+',isActive:1').then(response=>{
      VuexContext.commit('setOrderTable',response)
      return 'success'
    })
  },
  initOrdersTable(VuexContext){
   return getApi('orders?limit=600').then(response=>{
     VuexContext.commit('setOrderTable',response)
     return 'success'
   })
  },
  sendNewCustomer(VuexContext,payload){
    console.log(payload);
    
  return postApi('orders',payload).then(result=>{
     // console.log(result);
     VuexContext.commit('setNewCustomer', payload)
     return { status :"success", id : result.result }
    // console.log(payload);
    
   })
},
sendPutCustomer(veuxcontext,payload){
  // console.log(payload);
  
  putApi('orders',payload.id, {
  beleg : payload.beleg,
  companyId : payload.companyId,
  customerAddress :  payload.customerAddress,
  customerLastname : payload.customerLastname,
  customerName : payload.customerName,
  isAus: payload.isAus,
  isHaus : payload.isHaus,
  isExpress: payload.isExpress,
  isOldMachine : payload.isOldMachine,
  
  })
}
}

export default{
    state,
    getters,
    mutations,
    actions
}
