import Vue from "vue";
import Vuex from "vuex";
import { cryptoData } from "../../secure";
import { getApi,postApi, putApi } from "../../api/api";

Vue.use(Vuex);

const state = {
  // depo için gerekli olan mallar json içeriği veri tabanı olabilir
  goods:[],
  // hangi depoda hangi mallar bulunmakta json içeriği veri tabanı olabilir

  lagers: [],
  // https://gurhanarslan.github.io/elager/#/overview sayfasında Firma seçtikten sonra gelen sağ altta sarı renkte olan helper butonu
  //  ein aus yani ürünün depoya girişi çıkışları buradan yapılıyor json içeriği veri tabanı olabilir.
  productsBelegNum: [],
  // bunlar veri tabannında gerekli değil ön tarafta hallediliyor. 
  freeProduct:[],
  freeBranchProduct:[],
  branchId:null,
  id:0,
  nausgang:'branchOverview',
  wareHouseProduct:[]
};
const getters = {
  getwareHouseProduct(state){
    return state.wareHouseProduct;
  },
  getNausgang(state){
    // console.log(state.nausgang);
    
    return state.nausgang;
  },
  getQuantityProductInLagerForBranch(state){
   let selectedPdfData={
      branchName :"",
      data:[]
    }
  let  same =false
    let data=[]
    // console.log(state.branchId,'BRAID');
    
    state.lagers.forEach((res)=>{
        // console.log(state.branchId, res.branchId);
        
      if(state.branchId==res.branchId){

        // console.log('burdayız branchIdler eşit');
        
        selectedPdfData.branchName = state.branchId
          // console.log('branchNameyi Aldık',selectedPdfData.branchName);
        res.data.forEach((items)=>{
          // console.log('lagersin datalarına girdik ');
          
      data.push(items)
     })  
       }
     
   })
   // console.log('POPRLAR');
   
   for (var prop in data) {
     same = false
         if(selectedPdfData.data.length==0){
           selectedPdfData.data.push({...data[prop],menge:1})
         }
         else {
         for(var i in selectedPdfData.data){
           if(selectedPdfData.data[i].artikelNum == data[prop].artikelNum){
             selectedPdfData.data[i].menge ++
             same = true
           }
         }
         if(!same){
           selectedPdfData.data.push({...data[prop],menge:1})
         }
         }
          
       }
       return selectedPdfData
  },
  getLagers(state) {
   return state.lagers;
  },
  getFreeProduct(state){
    return state.freeProduct
  },
  getFreeBranchProduct(state){
    
    return state.freeBranchProduct;
  },
  getProductsBelegNum(state){
 
    return state.productsBelegNum;
  },
  getGoods(state){
    return state.goods;
  },
  getId(state){return state.id}

};
const mutations = {
  setLagers(state,payload){
    state.lagers.unshift(payload)
  },
  setId(state){state.id++;},
  setBranchId(state,payload){
    state.branchId = payload
  },
  setFreeProduct(state,payload){
      state.freeProduct.push(payload)
  },
  setNewItemsForLagerInBranch(state,payload){
    for (let i = 0; i < payload.items.length; i++) {
      // console.log(payload.items[i],'LAGERJS BURADAYIZ',payload.items.length,Number(payload.items[i].quantity));
      
      
      state.freeBranchProduct.push({...payload.items[i], branchId : payload.branchId, belegNo: payload.belegNo, isEin:true, einaus : (payload.einausIndex-1), einausDataIndex : i})
       
    }
    // console.log(state.freeBranchProduct);
   // localStorage.setItem('freep',JSON.stringify(state.freeBranchProduct))
   let cryptoItem = cryptoData(JSON.stringify(state.freeBranchProduct))
   localStorage.setItem('freep',cryptoItem)

    state.freeProduct=[]
    
  //  state.freeBranchProduct.push(paylaod)
  },
  setRemoveBranchItem(state,payload){
    state.freeBranchProduct.splice(payload,1)
  },
  setItemsInLager(state,payload){
    // console.log('INDEX DEGERİ ',payload);
  let  index = -1
   let pushIndex = 0
    for (let k = 0; k < state.lagers.length; k++) {
      if(state.lagers[k].branchId==payload.branchId){
          index++
      }
      if(index==payload.dataIndex){
          pushIndex = k
      }
    }
    // console.log(pushIndex,state.lagers[pushIndex]);
      
    for (let i = 0; i < payload.items.length; i++) {
      state.lagers[pushIndex].data.push(payload.items[i])
      let belegIndex = -1
      for (let u = 0; u < state.productsBelegNum.length; u++) {
        // console.log('ein');
        if(state.productsBelegNum[u].branchId== payload.branchId){
          belegIndex++
          if(belegIndex == payload.items[i].einaus){
            state.productsBelegNum[u].data[payload.items[i].einausDataIndex].stellplatz = pushIndex+1
            let gang = Math.ceil((pushIndex+1) / 12)
            state.productsBelegNum[u].data[payload.items[i].einausDataIndex].gang = gang
          }
        }
      }
    }
    for (let i = 0; i < payload.index.length; i++) {
      state.freeBranchProduct.splice(payload.index[i],1)
    }
  let cryptoItem = cryptoData(JSON.stringify(state.freeBranchProduct))
  localStorage.setItem('freep',cryptoItem)
  },
  setNewProblemItemsForLagerInBranch(state,payload){
    
      state.productsBelegNum.push({ data: payload.items, createdAt:payload.createdAt, ieEin : false , branchId : payload.branchId, belegNo: payload.belegNo})
   
  },
  setGoodsForProductsBelegNum(state,payload){
    
    state.productsBelegNum.push({ isEin : false , companyId : payload.companyId, belegNumber: payload.belegNumber})

  },
  setNewProduct(state,payload){
    state.goods.push(payload)
  },
  setInitFreeBranchProduct(state,payload){
    for (let i = 0; i < payload.length; i++) {
      // console.log(payload[i],'LAGERJS BURADAYIZ',payload.length);
      
      state.freeBranchProduct.push({...payload[i], branchId : payload.branchId, belegNo: payload[i].belegNo, isEin:true})
      
    }
  },
  setNewStellplatz(state,payload){
    state.lagers.push({...payload, id: (state.lagers[state.lagers.length-1].id+1)})
  },
  setProduct(state,payload){
    state.goods = payload.body
    // console.log(state.goods);
    
  },
  setWareHouse(state,payload){
    state.lagers = []
   if(payload.body==null) { state.lagers=[]}
   else {  state.lagers = payload.body}
    // console.log(state.lagers);
    
  },
  setWareHouseProduct(state,payload){
 // console.log(payload, 'SETWAREHOUSEPRODUCT');
 state.wareHouseProduct = payload
 
  },
  setProductsBelegNum(state,payload){
    // console.log(payload);
    
if(payload.body==null){     state.productsBelegNum = [] }
else {     state.productsBelegNum = payload.body}
  },
  setGoodsTable(state,payload){
    state.goods = payload.body
  },
  deleteItemsFreePrdocut(state,payload){
    for (let i = 0; i < payload.length; i++) {
      state.freeBranchProduct.splice(payload[i],1)
    }
  let cryptoItem = cryptoData(JSON.stringify(state.freeBranchProduct))
  localStorage.setItem('freep',cryptoItem)
  },
  };

const actions = {
  sendNewStellplatz(VuexContext,payload){
    // branchoverview.vue YENİ BİR STELLPLATZ EKLENDİĞİ ZAMAN 
   getApi('warehouse?sortby=stellplatz&order=desc&limit=1').then(result=>{
      if(result.body!=null){
        let stellplatz = (result.body[0].stellplatz+1)
        postApi('warehouse',{ companyId : payload.companyId, stellplatz : stellplatz  , gang :  Math.ceil(stellplatz/12) })
        .then(response=>{
          VuexContext.commit('setLagers', { id : response.result, companyId : payload.companyId, stellplatz : stellplatz  , gang :  Math.ceil(stellplatz/12) })
        })
      }
     
   })

  },
  sendFreeProduct(VuexContext,payload){
    // for (let i = 0; i < payload.quantity; i++) {
    //   VuexContext.commit('setFreeProduct', { artikelNum : payload.artikelNum , productName : payload.productName })
    // }   
    //   BELEG NUMARASI OLMADAN KAYIT EDİLMİŞ PRODUCTLAR ---- components/LagerLayouts/AddNewLagerProduct/AddNewLagerProduct.vue'de Kullanıldı.
  VuexContext.commit('setFreeProduct', payload)

  },
  sendNewItemsForLagerInBranch(VuexContext,payload){
    // BELEG NUMARASI İLE BRANCHA GÖRE OLUŞTURULAN PRODUCT 
    // -----  components/LagerLayouts/AddNewLagerProduct/AddNewLagerProduct.vue ' de kullanıldı
    // console.log(payload);
    
    VuexContext.commit('setNewItemsForLagerInBranch',payload)
  },
  removeFreeBranchItem(VuexContext,payload){
    // BELEG NUMARASI KAYIT EDİLDİKTEN SONRA DEPOLARA GÖNDERİLİRKEN DEPOYA EKLENMEKTEN VAZGEÇİLEN PRODUCTLARI SİLER. 
    // ----   components/LagerLayouts/getLagerFreeProductModal/getLagerFreeProductModal.vue
    VuexContext.commit('setRemoveBranchItem',payload)
},

sendNewProblemItemsForLagerInBranch(VuexContext,payload){
  //  components/LagerLayouts/wausBranchOverview.vue SIKINTILI PRODUCTLARI BASAN FUNCTION
  VuexContext.commit('setNewProblemItemsForLagerInBranch',payload)
},
sendGoodsForProductsBelegNum(VuexContext,payload){
     // BELEG NUMARASI İLE BRANCHA GÖRE OLUŞTURULAN PRODUCT BELEG NO YA GORE OLAN TABLOYA GÖNDERİLDİ
    // -----  components/LagerLayouts/AddNewLagerProduct/AddNewLagerProduct.vue ' de kullanıldı LİSTELEMESİ --- pages/overview.vue

       VuexContext.commit('setGoodsForProductsBelegNum',{...payload})
   
},
sendNewProduct(VuexContext,payload){
  // YENİ PRODUCT EKLEME 
  // ---- pages/overview.vue
  postApi('product',payload).then(result=>{
    // console.log(result);
    if(result.result==1){

  VuexContext.commit('setNewProduct',payload)
    }
    
  })

},
sendPutProduct(VuexContext,payload){
  return putApi('product',payload.id,{ artikelNum : payload.artikelNum, name :payload.name}).then(result=>{
    // console.log(result);
    
    return 'success'
  })
},
initProduct(VuexContext,payload){
  return getApi('product?fields=artikelNum, name, id, isActive&search='+payload).then(response=>{
     VuexContext.commit('setProduct',response)
     return 'success'
  })
},
initWareHouse(VuexContext,payload){

  return getApi('warehouse?filter=companyId:'+payload.branchId+'&limit='+payload.limit).then(response=>{
    VuexContext.commit('setWareHouse',response)
   
     return 'success'
  })
},
initWareHouseProduct(VuexContext,payload){

 return getApi('warehouseProducts?filter=warehouseId:'+payload).then(response=>{
  let data=[]
    let body = response.body
  if(body==null){
    data = []
    VuexContext.commit('setWareHouseProduct',data)
  }
  else {
    body.forEach(result=>{
      getApi('product/'+result.productId).then(res=>{
        // console.log(res);
        
        data.push({...res,warehouseDetails: result,warehouseProductsId: result.id})
        VuexContext.commit('setWareHouseProduct',data)
      })
    })
  }


    return 'success'
  })},
  sendDocumentTable(VuexContext,payload){
    return getApi('document?filter=companyId:'+payload).then(result=>{
       VuexContext.commit('setProductsBelegNum',result)
       return 'success'
    })
  },
  sendWareHouseProductForDocument(VuexContext,payload){
    return getApi('warehouseProducts?filter=documentId:'+payload).then(response=>{
      return response
    })
  },



};

export default {
  state,
  getters,
  mutations,
  actions
};
