<template>
  <div>
    <span
      v-if="selectedData.length!=0"
      style="position:fixed;right:10px;z-index:1050"
      :style="{ width: '350px!important', bottom: '20px' }"
    >
      <li 
        v-for="(data, index) in selectedData"
        style=" cursor: grab;list-style: none;"
        :key="index"
        class="my-2 parent border shadow p-3 bg-light rounded" 
        >
        <el-row >
          <el-col :span="8">
            {{ data.artikelNum }}
          </el-col>
          <el-col :span="8"> {{ data.name.substr(0, 8) }}.. </el-col>
          <el-col :span="4">
            <i
            @click="closeDragItems(data,index)"
              style="cursor:pointer;font-size:24px"
              class="text-danger fas fa-minus-circle"
            ></i>
          </el-col>
        </el-row>
      </li>
    </span>
    <el-row  class="mt-5">
      <el-col :span="12"
        ><h2>Filiale : {{ branchName }}</h2></el-col
      >
      <el-col v-if="getActiveUser.isLagerSchreiben" :span="12">
        <el-button type="primary" v-b-modal.newLagerProduct round>
          <i class="fas fa-plus"></i> Artikel Hinzufügen</el-button
        >
      </el-col>
    </el-row>
  <div class="p-5">
        <div class="text-center">
        <el-button
            class="my-2"
            @click="newStellplatz()"
            type="success"
            round
          >
            <i class="fas fa-plus"></i> Neu Stellplatz</el-button
          >
    </div>
    <el-collapse accordion v-model="activeNames">
  <el-collapse-item 
    :name="lager.stellplatz"
  class="p-3 border bg-light shadow my-3 bg-success"
      v-for="(lager, index) in getLagers"
      :key="index" >
      <template slot="title">
      <div @click="getLagerItems(lager.id)" class="text-left w-100 p-3 bg-light">
          <span>Stellplatz {{ lager.stellplatz }} - Gang {{lager.gang}}</span>
      </div>
      </template>
           <div  v-if="getActiveUser.isLagerSchreiben" class="text-center bg-light">
          <el-button
            class="my-2"
            @click="getLagerIndex(lager.id)"
            v-b-modal.getLagerFreeProduct
            type="success"
            round
          >
            <i class="fas fa-plus"></i> Artikel Hinzufügen</el-button
          >
        </div> 
        <div

           style="cursor:pointer;"
          @click="getActiveUser.isLagerZuruck ? showChangeStellplatzModal(data,lager,indexData):''"
       
          class="bg-white p-3 my-2 border-bottom"
          v-for="(data, indexData) in getwareHouseProduct"
          :key="indexData"
        >
          <div class="opinion-header h5">
            <span style="font-weight:800"
              >Artikelnummer : {{ data.artikelNum }}</span
            >
          </div>
          <div>
            <span>{{ data.name }}</span>
          </div>
        </div></el-collapse-item>
</el-collapse>
  </div>
  <div v-if="getLagers.length!=0">
   <div class="text-center" v-if="getLagers[getLagers.length-1].stellplatz!=1">
        <el-button
            class="my-2"
            @click="loadMoreStellplatz()"
            type="primary"
            round
          >
            <i class="fas fa-plus"></i> Mehr laden</el-button
          >
    </div>
  </div>
  
    <b-modal v-if="changeStellplatz.length!=0 && getLagers.length!=0" id="changeStellplatz" ref="changeStellplatz" hide-footer title="Stellplatz">
    <h4> 
       <strong> ArtikelNummer :  {{changeStellplatz.artikelNum}} </strong></h4>
      <h4>
        <strong> Artikel :  {{changeStellplatz.name}}</strong>
      </h4>
   <div>   <strong> Stellplatz : <input type="number" v-model.number="changeStellplatz.stellplatz"> </strong></div>
   <div class="text-right">
     <el-button :disabled="!usableStellplatz" @click="changeStellplatzMethods()" round :type="usableStellplatz?'primary':'danger'"> Speichern </el-button>
   </div>
    </b-modal>

    <new-lager-product-modal
      :branchName="branchName"
      :branchId="branchId"
      :index="index"
    ></new-lager-product-modal>
    <get-lager-free-product-modal
      :branchId="branchId"
      :index="index"
    ></get-lager-free-product-modal>
  </div>
</template>

<script>
import { putApi, getApi} from '../../api/api'
import { mapGetters } from "vuex";
import newLagerProductModal from "./addNewLagerProductModal/addNewLagerProductModal";
import getLagerFreeProductModal from "./getLagerFreeProductModal/getLagerFreeProductModal";
export default {
  components: {
    newLagerProductModal,
    getLagerFreeProductModal
  },
  props: ["getBranchLager", "branchName", "branchId"],
  computed: {
    ...mapGetters(["getwareHouseProduct","getProducts", "getLagers","getActiveUser","getQuantityProductInLagerForBranch","getProductsBelegNum"])
  },
  watch:{
    'changeStellplatz.stellplatz'(data){
     if(data!=""){
        getApi('warehouse/stellplatz/'+data+'?filter=companyId:'+this.branchId).then(result=>{
        if(result.count!=0){
          this.usableStellplatz = true
          this.warehouseId = result.body[0].id
        }
        else{
          this.usableStellplatz = false
        }
      })
     }
      
    },
    activeNames(data){
          // console.log(this.activeName,'AACTIVEEE NAMEE');
    if(data!=""){
      this.activeName.push(data)
         // console.log(this.activeName,'AACTIVEEE NAMEE');
    }
    },
    getwareHouseProduct(data,oldData){
           if(this.selectedData.length!=0 && data.length!=0){
          for (let i = 0; i < this.selectedData.length; i++) {
            if(this.selectedData[i].oldId==this.getwareHouseProduct[0][0].warehouseId && !this.selectedData[i].isValid && this.activeNames!=""){
            this.selectedData.splice(i,1)
          }
          if(this.selectedData[i].oldId!=this.getwareHouseProduct[0][0].warehouseId){
            this.selectedData[i].isValid=false
            
          }
        
        }
        for (let i = 1; i < this.activeName.length; i++) {
          if(this.activeName[i]==this.activeName[i-1]){
            this.selectedData=[]
        }
            this.activeName.splice(0,1)
          }
          
        
        }

      
    }
  },
  data() {
    return {
      percentage: 10,
      activeName:[],
      warehouseId:null,
      colors: [
        { color: "#28d43f", percentage: 20 },
        { color: "#84d428", percentage: 40 },
        { color: "#d4b428", percentage: 60 },
        { color: "#ff5e00", percentage: 80 },
        { color: "#d44a28", percentage: 100 }
      ],
      index: null,
      lagerIndex:null,
      over:false,
      selectedData: [],
      limit : 30,
      which:null,
      draggingData:[],
      activeNames:"",
      usableStellplatz:false,
      pulse:false,
      dragStartedItems:[],
      miniIndex:null,
      wareId:null,
      changeStellplatz:[]
    };
  },
  methods: {
    changeStellplatzMethods(){
      console.log(this.changeStellplatz);
      putApi('warehouseProducts',this.changeStellplatz.warehouseProductsId,{ warehouseId : this.warehouseId }).
      then(result=>{
        console.log(result);
        if(result.result==1){
          this.activeNames= ""
            this.$bvModal.hide('changeStellplatz')
            this.$notify({
          title: "Success",
          message: "Regal erfolgreich hinzugefügt",
          type: "success",
              duration: 7000
        });
        }
      })
      
      
    },
    showChangeStellplatzModal(row,lager,indexData){
      this.changeStellplatz = {...row,...lager,indexData:indexData}
      this.$bvModal.show('changeStellplatz')
    },
    loadMoreStellplatz(){
        this.limit = this.limit+20
          let req = { limit : this.limit , branchId : this.branchId }
    this.$store.dispatch('initWareHouse',req)
    },
    sendWareHouseProduct(id){
      // console.log(id,'WAREHOUSE ID TIKLADIGIN ID');
      
      this.$store.dispatch('initWareHouseProduct',id)
      this.wareId = id
      
    },
    newStellplatz(){
      this.$confirm('Möchten Sie eine Regalnummer hinzufügen?', 'Warning', {
          confirmButtonText: 'Ja',
          cancelButtonText: 'Nein',
          type: 'warning'
        }).then(() => {

         this.$store.dispatch('sendNewStellplatz',{ companyId : this.branchId }).then(result=>{
        if(result=='success'){
                this.$notify({
          title: "Success",
          message: "Regal erfolgreich hinzugefügt",
          type: "success",
              duration: 7000
        });
        }
     
          this.$message({
            type: 'success',
            message: 'erfolgreich'
          });

       })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: 'Storniert'
          });          
        });
     
    },
    getLagerItems(id){
 
  
            console.log(id);
            
      this.$store.dispatch('initWareHouseProduct',id)
      this.wareId = id

     
      
    },
    getLagerIndex(index) {
      // console.log(index);
      
      this.index = index;
    }
  },
  mounted(){
// console.log('MOUNTEDDDDD',this.branchId);
  let req = { limit : this.limit , branchId : this.branchId }
  console.log('MOUNTED',this.branchId,this.limit);
  
    this.$store.dispatch('initWareHouse',req)
  }
};
</script>

<style lang="scss" scoped>
.bg-light_success{
  background: rgb(170, 253, 170);
}
</style>
