<template>
  <div class="p-3">
    <el-row class="border shadow p-2">
      <el-col :md="6" :sm="24" :xs="24">
        <div class="w-100 bg-white p-3 mt-3 text-center">
          <span
          v-for="(groupFilter,index) in groupFilterItems" :key="index"
            :class="index==0? 'startRadius': (groupFilterItems.length-1)==index? 'finishRadius':''"
            style="cursor:pointer;font-weight:700"
            @click="lager=groupFilter"
            class="p-2 px-md-3 border transition"
            :style="lager==groupFilter ? {color: '#0063b2', backgroundColor : '#9cc3d5'} :  {color: '#89abe3', backgroundColor : '#fcf6f5'}"
          >{{groupFilter}} </span>
        </div>
      </el-col>
      <el-col class="text-center" :offset="1" :sm="23" :xs="23" :md="5">
        <el-select v-model="selectedBranch" class="w-100 mt-4" clearable placeholder="Select">
          <el-option
            v-for="item in selectBoxFilterItems"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </el-col>
      <el-col class="text-right mt-2" :offset="1" :sm="13" :xs="13" :md="4">
        <div class="eralp-form-control">
          <div class="eralp-input-container">
            <input v-model="searchBox" type="text" required />
            <label>{{search.label}}</label>
          </div>
        </div>
      </el-col>
      <el-col :sm="8" :xs="8" :md="3" class="my-3 ml-3">
        <el-dropdown>
          <el-button round type="primary">
            Filter
            <i class="el-icon-arrow-down el-icon--right"></i>
          </el-button>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item v-for="(dropdown,index) in dropdownFilterItems" :key="index">
              <div @click="searchingUser(dropdown)">{{dropdown.label}}</div>
            </el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </el-col>
      <el-col class="my-3 text-right" :xs="24" :sm="24" :md="2">
        <el-button type="primary" v-b-modal.newUser round><i class="fas fa-address-card"></i>  Neu User Anlegen</el-button>
      </el-col>
    </el-row>
    <el-table
      class="border shadow p-3 mt-3"
      :data="filtred"
      :default-sort="{prop: 'date', order: 'descending'}"
      style="width: 100%"
    >
      <el-table-column v-for="(data,index) in tableDataFields" :key="index" :prop="data.prop" :label="data.label" :sortable="data.sortable ? true:false"></el-table-column>
    </el-table>
  </div>
</template>

<script>
import { dateFormatter } from "../../dateFormatter";
import { mapGetters } from 'vuex';
import auth from '../../mixins/auth';
import isAdmin from '../../mixins/isGod';
export default {

  mixins:[auth,isAdmin],
    data() {
    return {
      lager: "All",
      editedUser: [],
      editUserIndex: null,
      category: false,
      selectedBranch: "",
      searchBox: "",
      search: { type: this.dropdownFilterItems[0].value, label: this.dropdownFilterItems[0].label },
      datePickerElement: null,
      editUserItems: [],
      filtredName: null
    };
  },
  props:{
      groupFilterItems:{
          type: Array,
          default : ()=>{
              return ['GFT1','GFT2','GFT3','GFT4']
          }
      },
           selectBoxFilterItems:{
          type: Array,
          default : ()=>{
              return [
                  {  value: "SBFI1",  label: "SBFI1"},
                  {  value: "SBFI2",  label: "SBFI2"},
                  {  value: "SBFI3",  label: "SBFI3"},
                  {  value: "SBFI4",  label: "SBFI4"}
              ]
          }
      },
            dropdownFilterItems:{
          type: Array,
          default : ()=>{
              return [
                  {  value: "DFI1",  label: "DFI1"},
                  {  value: "DFI2",  label: "DFI2"},
                  {  value: "DFI3",  label: "DFI3"},
                  {  value: "DFI4",  label: "DFI4"}
              ]
          }
      },
      tableData:{
          type:Array,
          default : ()=>{
              return [
              { id:5,
        date: "1611-05-03",
        DF1: "DF1",
        DF2: "DF2",
        DF3: "DF3",
        DF4:"DF4",
        TDF1:"123456",
        TDF2:"123456",
        branch: { id: 1, branchName: "All" },
        aktiv: false,
        anlegen: false,
        stornieren: false,
        belegnrandern: false,
        belegandern: false,
        lagerlogin: true,
        lagerschreiben: false,
        lagerartikel: true,
        lagerzua: false,
        lagerstornoBeleg: false,
        isAdmin:true}
          ]
          }
      },
      tableDataFields:{
          type: Array,
          default :()=>{
              return [
              {prop: 'DF1', label: "DF1", sortable : true},
              {prop: 'DF2', label: "DF2", sortable : false}
          ]
      }}
  },
  computed: {
    filtred() {
      if (this.lager != "All") {
        return this.tableData.filter(response => {
          if (response[this.lager]) {
            if (this.selectedBranch == "") {
              return String(response[this.search.type])
                .toLowerCase()
                .match(this.searchBox.toLowerCase());
            } else {
              if (response.companyId == this.selectedBranch) {
                return String(response[this.search.type])
                  .toLowerCase()
                  .match(this.searchBox.toLowerCase());
              }
            }
          }
        });
      } else {
        if (this.selectedBranch == "") {
          if (this.searchBox == "") {
            return this.tableData;
          } else {
            return this.tableData.filter(response => {
              return String(response[this.search.type])
                .toLowerCase()
                .match(this.searchBox.toLowerCase());
            });
          }
        } else {
          return this.tableData.filter(response => {
            if (response.companyId == this.selectedBranch) {
              return String(response[this.search.type])
                .toLowerCase()
                .match(this.searchBox.toLowerCase());
            }
          });
        }
      }
    },
    filtredBranch() {
      if (this.lager != "All") {
        return this.tableData.filter(response => {
          if (response[this.lager]) {
            if (this.selectedBranch == "") {
              return response;
            } else {
              if (response.companyId == this.selectedBranch) {
                return response;
              }
            }
          }
        });
      } else {
        if (this.selectedBranch == "") {
          return this.tableData;
        } else {
          return this.tableData.filter(response => {
            if (response.companyId == this.selectedBranch) {
              return response;
            }
          });
        }
      }
    }
  },
  methods: {
    searchingUser(type) {
    this.search = { type: type.value , label: type.label };
    },
    editUser(index) {
      //  let num = this.tableData.length
      //   num = num-1
      this.editUserIndex = index;
      this.editUserItems = this.tableData[index];
    }
  },

  watch: {
    editedUser(){
      // console.log(this.editedUser,'worked');
      
      this.tableData[this.editUserIndex] = this.editedUser
    },
    selectedBranch() {
      // console.log(this.selectedBranch);
    }
  }
};
</script>
