export const postApi = (dataURL,data) => {
    let token = store.getters.getToken;
    var myHeaders = new Headers();
myHeaders.append("Authorization", "Bearer "+token)

var raw = JSON.stringify(data);

var requestOptions = {
  method: 'POST',
  headers: myHeaders,
  body: raw,
  redirect: 'follow'
};

return fetch(API_URL_2+dataURL, requestOptions)
  .then(response => response.json())
  .then(result => {
    return result})
  .catch(error => console.log('error', error));
}
export const putApi = (dataURL,putId,data)=>{
    var myHeaders = new Headers();
    let token = store.getters.getToken;

myHeaders.append("Authorization", "Bearer "+token);

var raw = JSON.stringify(data);

var requestOptions = {
  method: 'PUT',
  headers: myHeaders,
  body: raw,
  redirect: 'follow'
};

return fetch(API_URL_1+dataURL+'/'+putId, requestOptions)
  .then(response => response.json())
  .then(result => {
    return result})
  .catch(error => console.log('error', error));

}
export const deleteApi = (dataURL,deleteId)=>{
    let token = store.getters.getToken;
    var myHeaders = new Headers();
myHeaders.append("Authorization", "Bearer "+token);

var raw = "";

var requestOptions = {
  method: 'DELETE',
  headers: myHeaders,
  body: raw,
  redirect: 'follow'
};

return fetch(API_URL_1+dataURL+'/'+deleteId, requestOptions)
  .then(response => response.json())
  .then(result => {
    return result})
  .catch(error => console.log('error', error));
}