import Vue from 'vue'
import Vuex from 'vuex'
import { cryptoData } from '../../secure';
import {getApi, postApi} from '../../api/api'

Vue.use(Vuex);

const state={
    users: [],
      isAuth:false,
      activeUser:[]
}
const getters= {
getUsers(state){
    return state.users;
},
getIsAuth(state){
  return state.isAuth;
},
getActiveUser(state){
  return state.activeUser 
}
}
const mutations= {
  setUserTable(state,payload){
    state.users = payload.body
  },
setToLogin(state,payload){
  state.isAuth = true,
  state.activeUser = payload
  const token = 'eralpsoftware'
 // localStorage.setItem('token','eralpsoftware')
 // localStorage.setItem('tokenId',JSON.stringify(payload))
 let tokenCrypto = cryptoData(token)
 localStorage.setItem('token',tokenCrypto)
 let userCrypto = cryptoData(JSON.stringify(payload))
 localStorage.setItem('tokenId',userCrypto)

},
setActiveUser(state,payload){
  console.log(payload);
  
  state.activeUser  =payload
},
hasToken(state,payload){
  state.isAuth = payload

  
},
setNewUser(state,payload){
  state.users.unshift(payload)
},
}

const actions= {
  initUserTable(VuexContext){

   return getApi('user').then(user=>{
     VuexContext.commit('setUserTable',user)
     console.log(user);
      return 'success'
   }).catch(err=>{
     console.log(err);
     return 'err'
   })
    
  },
  sendNewUser(VuexContext,payload){
    //APİ İŞLEMLERİ
    console.log(payload);
  return  postApi('user',payload).then(result=>{
      if(result.result!=null){
        VuexContext.commit('setNewUser',payload)
        return 'success'
      }
    })
    

  },
 toLogin(VuexContext,payload){
   let user = VuexContext.state.users;
   let loginUser = 'fail'
   console.log(payload);
   let companyName =""
    user.filter(response =>{
      console.log(user);
      
      if(response.username == payload.username && response.password == payload.password && response.isActive)
      {
        console.log(payload,response.username,response.password);
       if(response.companyId==0){
         companyName = "All"
       }
       else {
        VuexContext.rootState.branch.company.forEach(company=>{
          console.log(company.id,response.companyId);
          
          if(response.companyId==company.id){
            console.log('Company id user companyıd eşleşti',company.name);
            
            companyName = company.name
          }
        })
       }
        VuexContext.commit('setToLogin',{...response,companyName:companyName})
        loginUser = "success"
      }
    })
    return loginUser
 },
 hasToken(VuexContext,payload){
  VuexContext.commit('hasToken',payload)
VuexContext.commit('setCustomersForBranch', VuexContext.state.activeUser)
 
  
 },
 toLogout(VuexContext){
   localStorage.removeItem('tokenId');
   localStorage.removeItem('token');
  VuexContext.state.isAuth = false
  VuexContext.rootState.customers.customersAll=[]
  VuexContext.state.activeUser = []
   return 'Logout'
 }
}

export default{
    state,
    getters,
    mutations,
    actions
}